@font-face {
  font-family: "Work Sans";
  src: local("Work Sans");
  src: url("../fonts/worksans.woff") format("woff"), url("../fonts/worksans.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  src: local("Work Sans");
  src: url("../fonts/worksans-medium.woff") format("woff"), url("../fonts/worksans-medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Work Sans";
  src: local("Work Sans");
  src: url("../fonts/worksans-bold.woff") format("woff"), url("../fonts/worksans-bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html,
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Work Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #151c22;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }
